import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { EnrollmentScorecardComponent } from './enrollment-scorecard.component';

const routes: Routes = [{ path: '', component: EnrollmentScorecardComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EnrollmentScorecardRoutingModule { }
